.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #F5F5F5; /* Light gray background */
  }
  
  .illustration-container {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .form-container {
    flex: 1;
    padding: 40px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .form-container h1 {
    font-size: 2rem;
    margin-bottom: 20px;
    color: #333;
  }

  .upstop{
    color: #6358dc;
    font-size: 40px;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }

  .error-message{
    color: red;
    font-size: 20px;
  }
  
  .social-login {
    display: flex;
    
    align-items: center;
    gap: 15px;
    margin-bottom: 30px;
    
  }
  
  .social-button {
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 100px;
    padding: 12px 25px;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .social-button:hover {
    background-color: #f0f0f0;
  }
  
  .social-button img {
    margin-right: 5px;
  }
  
  .input-group {
    margin-bottom: 20px;
  }
  
  .input-group label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
    color: #333;
  }
  
  .input-group input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  
  .remember-me {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .remember-me input {
    margin-right: 5px;
  }
  
  .primary-button {
    background-color: #5C6BC0; /* Purple color */
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 12px 30px;
    cursor: pointer;
    transition: all 0.3s ease;
  }

  .button-text{
    margin-bottom: 10px;
  }

  .img-1{
    width: 35px;
  }
  
  .primary-button:hover {
    background-color: #4C5BA0;
  }
  
  @media (max-width: 768px) {
    .login-container {
      flex-direction: column;
    }
  
    .illustration-container {
      width: 100%;
      padding: 20px;
    }
  
    .form-container {
      width: 100%;
      margin-top: 20px;
    }
  }